.row { margin-bottom: 2rem; }

.row:after { content: ''; clear: both; display: table; }

.row [class*="col-"] { background: #ccc; color: #000; float: left; padding: .5rem 1.5rem; }

.row [class*="col-"]:not(:last-child) { margin-right: 2rem; }

@media screen and (max-width: 425px) { .row [class*="col-"]:not(:last-child) { margin-bottom: 2rem; margin-right: 0; } }

@media screen and (max-width: 425px) { .row [class*="col-"] { float: none; min-width: 100%; } }

