html, body { height: 100%; }

html { font-size: 62.5%; }

body { color: #000; font-family: "Poppins", sans-serif; font-size: 1.6rem; line-height: 1.7rem; box-sizing: border-box; }

button, select, label[for], input[type='submit'], input[type='image'] { color: inherit; cursor: pointer; outline: none !important; }

input[type=number]::-webkit-inner-spin-button, input[type=number]::-webkit-outer-spin-button { -webkit-appearance: none; margin: 0; }

input[type='number'] { -moz-appearance: textfield; }

input::-ms-clear { display: none; }

:disabled, [disabled], [aria-disabled="true"] { cursor: not-allowed; }

a { color: inherit; text-decoration: none; }

a:hover { text-decoration: none; }

strong { font-weight: 700; }

b { font-weight: 600; }

input, select, button, textarea { font: inherit; outline: none; }

img { height: auto; max-width: 100%; }

/*** PRE-DEFINED CLASSES ***/
.inline { display: inline-block; }

.hidden { display: none; }

.disabled { pointer-events: none; }

.pull-left { float: left; }

.pull-right { float: right; }

.clearfix:after { content: ""; display: table; clear: both; }

@-webkit-keyframes scroll { 0% { -webkit-transform: translate(0, 0);
    transform: translate(0, 0); }
  100% { -webkit-transform: translate(-100%, 0);
    transform: translate(-100%, 0); } }

@-moz-keyframes scroll { 0% { -moz-transform: translate(0, 0);
    transform: translate(0, 0); }
  100% { -moz-transform: translate(-100%, 0);
    transform: translate(-100%, 0); } }

@keyframes scroll { 0% { transform: translate(0, 0); }
  100% { transform: translate(-100%, 0); } }

header { background: #f0f0f0; height: 75rem; min-width: 320px; order: 1; width: 100%; }

footer { background: #000; height: 8rem; min-width: 320px; order: 3; width: 100%; }

.main-wrapper { padding: 0 0 0 0; min-width: 320px; width: 100%; position: relative; overflow: hidden; min-height: 100%; display: flex; flex-direction: column; justify-content: flex-start; }

.wrapper { min-width: 320px; max-width: 1230px; padding: 0 2rem; margin: 0 auto; position: relative; }

/* titles */
p { font-size: 1.6rem; line-height: 1.8rem; color: #696969; padding: 1.5rem 0; font-weight: 400; }

h1 { font-size: 6rem; line-height: 8rem; color: #2d2d43; font-family: "Poppins", sans-serif; padding: 1.5rem 0; font-weight: 400; }

h2 { font-size: 3.6rem; line-height: 4.8rem; color: #2d2d43; font-family: "Poppins", sans-serif; padding: 1.5rem 0; font-weight: 400; }

h3 { font-size: 2.4rem; line-height: 6rem; color: #2d2d43; font-family: "Poppins", sans-serif; padding: 1.5rem 0; font-weight: 400; }

/* text position */
.text-left { text-align: left !important; }

.text-center { text-align: center !important; }

.text-right { text-align: right !important; }

.nowrap { white-space: nowrap !important; }

/* loader */
.loaded .main-wrapper { visibility: hidden; }

.icon-load { background: url(../img/loader.gif) no-repeat left top; width: 4rem; height: 4rem; position: fixed; left: 50%; top: 50%; margin-left: -2rem; display: none; }

.loaded .icon-load { display: block; }

/*
================================================================================
|                                     CONTENT                                 |
================================================================================
*/
.content { background: #fff000; flex-grow: 1; min-width: 320px; order: 2; text-align: left; width: 100%; }

.truncate { display: inline-block; max-width: 100%; overflow: hidden; text-overflow: ellipsis; vertical-align: top; white-space: nowrap; width: 100%; }

.tooltip { position: relative; }

.tooltip:before { background: inherit; border: inherit; border-right: 0; border-bottom: 0; content: ''; padding: .35em; position: absolute; top: -.4em; left: 1em; transform: rotate(45deg); }

.caret { border-top: 4px solid; border-right: 4px solid transparent; border-left: 4px solid transparent; display: inline-block; height: 0; margin-left: .2rem; vertical-align: middle; width: 0; }
